class Animate {
	constructor(selector) {
		this.$elements = document.querySelectorAll(selector);
		this.follow();
		window.addEventListener('scroll', () => this.follow());
		window.addEventListener('load ', () => this.follow());
		window.addEventListener('resize', () => this.follow());
	}
	follow() {
		if ( window.innerWidth < 992 ) {
			return false
		}
		let scrollTop = window.pageYOffset;

		this.$elements.forEach($element => {
			let offset = $element.dataset.offset ? parseInt($element.dataset.offset) : 0;
			let duration = $element.dataset.duration ? $element.dataset.duration : '1s';
			let delay = $element.dataset.delay ? $element.dataset.delay : '0s';
			let name = $element.dataset.animate ? $element.dataset.animate : '1s';
			console.log(offset)
			let top = $element.getBoundingClientRect().top + window.pageYOffset + offset - window.innerHeight;
			console.log('scrollTop - ', scrollTop, '; top - ', top)
			if (scrollTop > top && !$element.classList.contains('is-animated')) {
				$element.style.animationName = name;
				$element.style.animationDuration = duration;
				$element.style.animationDelay = delay;
				if (delay) {
					let timeTimeOut = delay.match(/\d+(?:\.\d+)?/g).map(Number) * 1000;
					setTimeout(() => {
						$element.classList.add('is-animated');
					}, timeTimeOut)
				}
			}
		});
	}
}

document.addEventListener('DOMContentLoaded', function () {
	if (document.querySelectorAll('[data-animate]').length) {
		new Animate('[data-animate]');
	}
});
